@use '../../styles/theme.scss';

.kanban-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  background: var(--project-bg-secondary);
  padding: 24px;

  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 32px;
    padding-bottom: 16px;
    border-bottom: 2px solid var(--project-border-color);

    h1 {
      margin: 0;
      font-size: 28px;
      font-weight: 700;
      color: var(--project-text-primary);
      background: var(--project-gradient-primary);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
    }

    button {
      display: flex;
      align-items: center;
      gap: 8px;
      padding: 12px 24px;
      background: var(--project-gradient-primary);
      color: white;
      border: none;
      border-radius: 12px;
      font-size: 14px;
      font-weight: 600;
      cursor: pointer;
      transition: all 0.3s ease;
      box-shadow: 0 4px 12px var(--project-shadow);

      &:hover {
        transform: translateY(-2px);
        box-shadow: 0 8px 24px var(--project-shadow);
      }

      mat-icon {
        font-size: 20px;
        width: 20px;
        height: 20px;
      }
    }
  }

  .loading-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1;
  }

  .kanban-board {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 24px;
    flex: 1;
    overflow: hidden;

    .kanban-column {
      display: flex;
      flex-direction: column;
      background: var(--project-bg-primary);
      border: 1px solid var(--project-border-color);
      border-radius: 16px;
      padding: 20px;
      min-height: 0;
      transition: all 0.3s ease;

      .column-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 16px;
        border-radius: 12px;
        margin-bottom: 20px;
        background: var(--project-gradient-primary);
        box-shadow: 0 4px 12px var(--project-shadow);

        h2 {
          margin: 0;
          font-size: 18px;
          font-weight: 700;
          color: white;
        }

        .count {
          background: rgba(255, 255, 255, 0.3);
          backdrop-filter: blur(8px);
          color: white;
          padding: 4px 12px;
          border-radius: 16px;
          font-size: 13px;
          font-weight: 700;
          min-width: 28px;
          text-align: center;
        }

        &.receive {
          background: linear-gradient(135deg, #ff9800 0%, #f57c00 100%);
        }

        &.progress {
          background: linear-gradient(135deg, #2196f3 0%, #1976d2 100%);
        }

        &.complete {
          background: linear-gradient(135deg, #4caf50 0%, #388e3c 100%);
        }
      }

      .project-list {
        flex: 1;
        overflow-y: auto;
        min-height: 200px;
        padding: 4px;

        &::-webkit-scrollbar {
          width: 6px;
        }

        &::-webkit-scrollbar-track {
          background: var(--project-bg-secondary);
          border-radius: 3px;
        }

        &::-webkit-scrollbar-thumb {
          background: var(--project-primary);
          border-radius: 3px;

          &:hover {
            background: var(--project-primary-dark);
          }
        }

        .project-item {
          margin-bottom: 12px;
          animation: fadeIn 0.3s ease;

          mat-card {
            cursor: move;
            background: var(--project-bg-primary);
            border: 1px solid var(--project-border-color);
            border-radius: 12px;
            transition: all 0.3s ease;
            box-shadow: 0 2px 8px var(--project-shadow);

            &:hover {
              transform: translateY(-4px);
              box-shadow: 0 8px 20px var(--project-shadow);
              border-color: var(--project-primary);
            }

            mat-card-header {
              margin-bottom: 12px;

              mat-card-title {
                font-size: 16px;
                font-weight: 700;
                color: var(--project-text-primary);
                line-height: 1.4;
              }
            }

            mat-card-content {
              .description {
                font-size: 14px;
                color: var(--project-text-secondary);
                line-height: 1.6;
                margin-bottom: 16px;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                line-clamp: 2;
                -webkit-box-orient: vertical;
                overflow: hidden;
              }

              .dates {
                display: flex;
                align-items: center;
                gap: 6px;
                font-size: 12px;
                color: var(--project-text-secondary);
                padding: 8px 12px;
                background: var(--project-bg-secondary);
                border-radius: 8px;

                mat-icon {
                  font-size: 16px;
                  width: 16px;
                  height: 16px;
                  color: var(--project-primary);
                }
              }
            }

            mat-card-actions {
              margin: 0 -16px -16px -16px;
              padding: 12px 16px;
              border-top: 1px solid var(--project-border-color);
              background: var(--project-bg-secondary);

              button {
                color: var(--project-primary);
                font-weight: 600;
                transition: all 0.3s ease;

                &:hover {
                  background: var(--project-primary-light);
                  transform: scale(1.05);
                }
              }
            }
          }
        }

        &.cdk-drop-list-dragging .project-item:not(.cdk-drag-placeholder) {
          transition: transform 250ms cubic-bezier(0, 0, 0.2, 1);
        }
      }
    }
  }
}

// Drag & Drop 스타일
.cdk-drag-preview {
  box-shadow: 0 12px 32px rgba(0, 0, 0, 0.3);
  opacity: 0.95;
  transform: rotate(3deg);

  mat-card {
    background: var(--project-bg-primary);
    border: 2px solid var(--project-primary);
  }
}

.cdk-drag-placeholder {
  opacity: 0.3;
  border: 2px dashed var(--project-primary);
  background: var(--project-bg-secondary);
  border-radius: 12px;

  mat-card {
    visibility: hidden;
  }
}

.cdk-drag-animating {
  transition: transform 250ms cubic-bezier(0, 0, 0.2, 1);
}

.cdk-drop-list-dragging .project-item {
  &:not(.cdk-drag-placeholder) {
    opacity: 0.7;
  }
}

// 반응형
@media (max-width: 1024px) {
  .kanban-container {
    padding: 16px;

    .header {
      flex-direction: column;
      gap: 16px;
      align-items: flex-start;

      h1 {
        font-size: 24px;
      }

      button {
        width: 100%;
        justify-content: center;
      }
    }

    .kanban-board {
      grid-template-columns: 1fr;
      overflow-y: auto;

      .kanban-column {
        min-height: 400px;
      }
    }
  }
}

@media (max-width: 768px) {
  .kanban-container {
    padding: 12px;

    .header {
      margin-bottom: 20px;

      h1 {
        font-size: 20px;
      }

      button {
        padding: 10px 20px;
        font-size: 13px;
      }
    }

    .kanban-board {
      gap: 16px;

      .kanban-column {
        padding: 16px;
        border-radius: 12px;

        .column-header {
          padding: 12px;

          h2 {
            font-size: 16px;
          }
        }
      }
    }
  }
}
